<svelte:head>
	<!-- <title>Miracle I Nnaoma</title> -->
	 <title>Dashboard</title>
</svelte:head>

<script>
	import Overview from "./pages/dashboard/Overview.svelte";
	import Deposit from "./pages/dashboard/Deposit.svelte";
	import Profile from "./pages/dashboard/Profile.svelte";
	import Transactions from "./pages/dashboard/Transactions.svelte";
	import Withdraw from "./pages/dashboard/Withdraw.svelte";
	import Settings from "./pages/dashboard/Settings.svelte";
	import IDVerification from "./pages/dashboard/IDVerification.svelte";
	import Purchase from "./pages/dashboard/Purchase.svelte";
	import Spinkit from "./components/Spinkit.svelte";
	import LoadingError from "./components/LoadingError.svelte";

	import { onDestroy, onMount } from "svelte";
	import { /*fakeLogin,*/ isStringEmptyOrWhitespace, basicDetails, fetchAndSendDataWithJson, changeLanguage, NetworkStates, user_verification_status } from "./scripts/general_scripts";

	import router from "page";

	let page
	let params

	let state
	let userData

	let errorMessage;

	router('/verification', () => {
		params = undefined
		page = IDVerification
	});

	router('/purchase-plan', () => {
		params = undefined
		page = Purchase
	});

	router('/dashboard', () => {
		params = undefined
		page = Overview
	});
	router('/deposit', () => {
		params = undefined
		page = Deposit
	});
	router('/deposit/:method/:amount', (ctx, next) => {
		params = ctx.params
		next()
	},  () => page = Deposit);
	router('/profile', () => {
		params = undefined
		page = Profile
	});
	router("/transactions", () => {
		params = undefined
		page = Transactions
	});

	router("/withdraw", () => {
		params = undefined
		page = Withdraw
	});

	router("/settings", () => {
		params = undefined
		page = Settings
	});

	router.start()

	async function loadItems() {
		state = NetworkStates.loading
        if (isStringEmptyOrWhitespace($basicDetails.firstname)) {
            let result = await fetchAndSendDataWithJson("/basic-details")
            if (result.status) {
				changeLanguage(result.data.language)
                $basicDetails = result.data
            }
        }

		let result = await fetchAndSendDataWithJson("/verification")
		if (result.status) {
			userData = result.data;
			if (userData.status != user_verification_status.verified && window.location.pathname != '/verification') {
				page = IDVerification;
				location.assign('/verification');
			} else if (userData.status == user_verification_status.verified && window.location.pathname == '/verification') {
				page = Overview;
				location.assign('/dashboard');
			}
			state = NetworkStates.loaded;
		} else {
			errorMessage = result.message;
			state = NetworkStates.error;
		}
	}

	onMount(async() => {
		// fakeLogin();
		loadItems()
    });

	onDestroy(() => {
		$basicDetails = {firstname: "", lastname: "", picture: "", date: "", paymentMethods: []}
	})
</script>

{#if state == NetworkStates.loaded}
	<svelte:component this={page} {params} status={userData.status}/>
{:else if state == NetworkStates.loading}
	<div class="w-100 h-100 background-color d-flex justify-content-center align-items-center">
		<Spinkit />
	</div>
{:else}
	<div class="w-100 h-100 background-color d-flex justify-content-center align-items-center">
		<LoadingError message={errorMessage} on:click={loadItems} />
	</div>
{/if}