<script>
    import Footer from "./../../components/DashboardFooter.svelte"
    import Navigator from "./../../components/DashboardNavigator.svelte"
    import Header from "./../../components/DashboardHeader.svelte"
    import Spinkit from "../../components/Spinkit.svelte"
    import LoadingError from "../../components/LoadingError.svelte"
    import {alertUser, NetworkStates, fetchAndSendDataWithJson, sendDataAndFile, fileTypes} from "../../scripts/general_scripts"
    import { onMount } from "svelte";

    let state
    let errorMessage

    let data = { 
        firstname : "", 
        lastname : "", 
        email : "", 
        mobile : "",
        address : "",
        state : "",
        picture: "/assets/img/pictures/default.png",
        country : ""
    };

    let editBasicContact = false;
    $: borderlessInput = editBasicContact == false;
    $: editBasicContactTitle = editBasicContact ? "Cancel" : "Edit";

    let editContactDetails = false;
    $: borderlessInputContact = editContactDetails == false;
    $: editContactDetailsTitle = editContactDetails ? "Cancel" : "Edit";

    let isSubmittingProfilePhoto = false;

    let imageForm;
    let imageHolder;
    let isSubmitable = false;

    $: changePhotoButtonText = isSubmitable ? "Confirm" : "Change Photo"

    const fetchData = async () => {
        state = NetworkStates.loading;
        let result = await fetchAndSendDataWithJson("/profile");
        if (result.status) {
            state = NetworkStates.loaded;
            data = result.data;
        } else {
            state = NetworkStates.error;
            errorMessage = result.message;
        }
    }

    const submitNewProfilePicture = async (evt) => {
        if (changePhotoButtonText.includes("Confirm")) {
            isSubmitable = false;
            isSubmittingProfilePhoto = true;
            let result = await sendDataAndFile("/profile-picture", imageForm);
            isSubmittingProfilePhoto = false;
            if (!result.status) {
                alertUser("Unable to update picture at this time", result.message, "error");
            } else {
                // window.location.reload()
                imageHolder.src = result.data.picture;
            }
        } else {
            imageForm.file.click();
        }
    }

    onMount(() => {
        fetchData()
    });
</script>

<div id="wrapper">
        <!-- navigator slot -->
        <Navigator active="profile"/>
        <div class="d-flex flex-column" id="content-wrapper">
            <div id="content" class="background-color">
                <!-- Header slot -->
                <Header />
                {#if state === NetworkStates.loaded}
                    <div class="container-fluid">
                        <h3 class="mb-4 text-white">Profile</h3>
                        <div class="row mb-3">
                            <div class="col-lg-4">
                                <div class="card mb-3">
                                    <form on:submit|preventDefault bind:this={imageForm} on:reset|preventDefault={() => {
                                        imageHolder.src = data.picture;
                                        isSubmitable = false;
                                    }}>
                                        <div class="card-body text-center shadow content-color">
                                            <img class="rounded-circle mb-3 mt-4" src={data.picture} width="160" height="160" bind:this={imageHolder} alt="profile_picture">
                                            <input type="file" name="file" class="d-none" on:change={(evt) => {
                                                const file = evt.target.files[0]
                                                    if(fileTypes.includes(file.type)){
                                                        imageHolder.src = URL.createObjectURL(file)
                                                        isSubmitable = true;
                                                    }else{
                                                        evt.target.value = null;
                                                        isSubmitable = false;
                                                        imageHolder.src = data.picture;
                                                        alertUser("File Format", "Invalid Image Format", "error");
                                                    }
                                            }}>
                                            <div class="mb-3">
                                                <button class="btn btn-primary btn-sm" disabled={isSubmittingProfilePhoto} type="button" on:click={submitNewProfilePicture}>
                                                    <span class="spinner-grow spinner-grow-sm" class:d-none={!isSubmittingProfilePhoto} role="status" aria-hidden="true"></span> {changePhotoButtonText}
                                                </button>
                                            </div>
                                            <div class:d-none={!isSubmitable}>
                                                <button class="text-button" style="color: var(--orange-red);" on:click={() => {
                                                    imageForm.reset();
                                                }}>
                                                    Cancel
                                                </button>
                                            </div>
                                        </div>
                                    </form>
                                </div>
                            </div>
                            <div class="col-lg-8">
                                <!-- Another column could go here -->
                                <div class="row">
                                    <div class="col">
                                        <div class="card shadow mb-3 content-color">
                                            <div class="card-header py-3 content-color d-flex justify-content-between align-items-center">
                                                <p class="m-0 fw-bold">Basic Details</p>
                                                <button class="text-button d-none" on:click={() => {editBasicContact = !editBasicContact}}>
                                                    {editBasicContactTitle}
                                                </button>
                                            </div>
                                            <div class="card-body">
                                                <form>
                                                    <div class="row">
                                                        <div class="col">
                                                            <div class="mb-3"><label class="form-label" for="firstname"><strong>Firstname</strong></label><input class="get-started-input-fields text-capitalize" class:borderless-input={borderlessInput} disabled={borderlessInput} type="text" id="firstname" placeholder="John" name="firstname" bind:value={data.firstname}></div>
                                                        </div>
                                                        <div class="col">
                                                            <div class="mb-3"><label class="form-label" for="lastname"><strong>Lastname</strong></label><input class="get-started-input-fields text-capitalize" class:borderless-input={borderlessInput} disabled={borderlessInput} type="text" id="lastname" placeholder="Doe" name="lastname" bind:value={data.lastname}></div>
                                                        </div>
                                                    </div>
                                                    <div class="row">
                                                        <div class="col">
                                                            <div class="mb-3"><label class="form-label" for="email"><strong>Email</strong></label><input class="get-started-input-fields text-capitalize" class:borderless-input={borderlessInput} disabled={borderlessInput} type="email" id="email" placeholder="example@email.com" name="email" bind:value={data.email}></div>
                                                        </div>
                                                        <div class="col">
                                                            <div class="mb-3"><label class="form-label" for="tel"><strong>Phone</strong></label><input class="get-started-input-fields text-capitalize" class:borderless-input={borderlessInput} disabled={borderlessInput} type="text" id="tel" placeholder="" name="tel" bind:value={data.mobile}></div>
                                                        </div>
                                                    </div>
                                                    <div class="mb-3 mt-2" class:hidden-display={borderlessInput}>
                                                        <button class="btn btn-primary btn-sm" type="submit">
                                                            <span class="spinner-grow spinner-grow-sm" role="status" aria-hidden="true"></span> Save Settings
                                                        </button>
                                                    </div>
                                                </form>
                                            </div>
                                        </div>
                                        <div class="card shadow content-color">
                                            <div class="card-header py-3 content-color d-flex justify-content-between align-items-center">
                                                <p class="m-0 fw-bold">Contact Details</p>
                                                <button class="text-button d-none" on:click={() => {/*editContactDetails = !editContactDetails */ alertUser("", "To update contact details, please reach out to support.") }}>
                                                    {editContactDetailsTitle}
                                                </button>
                                            </div>
                                            <div class="card-body">
                                                <form>
                                                    <div class="mb-3"><label class="form-label" for="address"><strong>Address</strong></label><input class="get-started-input-fields text-capitalize" class:borderless-input={borderlessInputContact} disabled={borderlessInputContact} type="text" id="address" placeholder="" name="address" bind:value={data.address}></div>
                                                    <div class="row">
                                                        <div class="col">
                                                            <div class="mb-3"><label class="form-label" for="city"><strong>State</strong></label><input class="get-started-input-fields text-capitalize" class:borderless-input={borderlessInputContact} disabled={borderlessInputContact} type="text" id="city" placeholder="Los Angeles" name="city" bind:value={data.state}></div>
                                                        </div>
                                                        <div class="col">
                                                            <div class="mb-3"><label class="form-label" for="country"><strong>Country</strong></label><input class="get-started-input-fields text-capitalize" class:borderless-input={borderlessInputContact} disabled={borderlessInputContact} type="text" id="country" placeholder="USA" name="country" bind:value={data.country}></div>
                                                        </div>
                                                    </div>
                                                    <div class="mb-3" class:hidden-display={borderlessInputContact}><button class="btn btn-primary btn-sm" type="submit">Save&nbsp;Settings</button></div>
                                                </form>
                                            </div>
                                        </div>
                                    </div>
                                </div>
                            </div>
                        </div>
                    </div>
                {:else if state == NetworkStates.loading}
                    <Spinkit />
                {:else}
                    <!-- Error component -->
                     <LoadingError message={errorMessage} on:click={fetchData}/>
                {/if}
            </div>
        <Footer />
    </div>
</div>