<script>
    import { fade } from "svelte/transition";

    export let showModal = false;
</script>

<div class="modal" class:show-modal={showModal} class:hide-modal={!showModal}>

    <!-- Modal content -->
    <div class="modal-content" transition:fade>
      <span class="close" on:click|self={() => {showModal = false}} on:keyup|self>&times;</span>
      <!-- SLot here -->
       <slot />
    </div>
  
</div>

<style>
    /* Modal background styling */
    .modal {
        position: fixed; 
        z-index: 1; 
        left: 0;
        top: 0;
        align-items: center;
        justify-content: center;
        width: 100%;
        height: 100%;
        background-color: rgb(0,0,0);
        background-color: rgba(0,0,0,0.4);
    }

    .show-modal {
        display: flex;
    }

    .hide-modal {
        display: none;
    }

    /* Modal content styling */
    .modal-content {
        position: relative;
        background-color: var(--grey);
        /* border: 1px solid #888; */
        max-width: 85%;
        /* max-width: 400px; */
        height: fit-content;
        overflow: auto;
        max-height: 100%;
        border-radius: 10px;
        padding: 10px 10px;
    }

    /* Close button styling */
    .close {
        position: absolute;
        top: 0;
        right: 15px;
        color: var(--semi-white);
        font-size: 28px;
        font-weight: bold;
        text-align: end;
        cursor: pointer;
    }

    .close:hover,
    .close:focus {
        color: var(--white);
        font-size: 30px;
        text-decoration: none;
        cursor: pointer;
    }
</style>