<script>
    import Footer from "./../../components/DashboardFooter.svelte"
    import Navigator from "./../../components/DashboardNavigator.svelte"
    import Header from "./../../components/DashboardHeader.svelte"
    import HorizontalLoader from "../../components/HorizontalLoader.svelte"
    import {NetworkStates, fetchAndSendDataWithJsonAndQuery, formatUTCDate, basicDetails, actualCurrency} from "../../scripts/general_scripts"

    let state;
    let page = 1;
    let limit = "10";

    $: fetchData(limit, page)

    let totalResults = 0;
    let transactions = [];
    let displayResultsText = "Showing 0 to 0 of 0";

    $: {
        const currentCount = page * limit
        displayResultsText = `Showing ${((page - 1) * limit) + 1} to ${(currentCount <= totalResults ? currentCount : totalResults)} of ${totalResults}`
    }
    $: pageCount = (totalResults/limit < 1 ? 1 : Math.ceil(totalResults/limit))
    $: pagesRange = Array.from({ length: pageCount - 1 + 1 }, (_, i) => 1 + i);


    const fetchData = async () => {
        state = NetworkStates.loading
        let result = await fetchAndSendDataWithJsonAndQuery("/transactions", {page, limit})
        if (result.status) {
            totalResults = result.totalTransactions
            transactions = result.data.results
            state = NetworkStates.loaded
        } else {
            state = NetworkStates.error
        }
    }
</script>

<div id="wrapper">
    <!-- Navigator here -->
    <Navigator active="transactions"/>
    <div class="d-flex flex-column" id="content-wrapper">
        <div id="content" class="background-color">
            <!-- Header -->
            <Header />
            <div class="container-fluid mb-5">
                <h3 class="mb-4 text-white">Transactions</h3>
                <div class="card shadow">
                    <div class="card-header py-3 content-color">
                        <p class="m-0 fw-bold">History</p>
                    </div>
                    <div class="card-body content-color">
                        <div class="row">
                            <div class="col-md-6 text-nowrap">
                                <div id="dataTable_length" class="dataTables_length" aria-controls="dataTable">
                                    <label class="form-label">Show&nbsp;<select class="d-inline-block form-select form-select-sm" bind:value={limit}>
                                            <option value="10">10</option>
                                            <option value="20">20</option>
                                            <option value="30">30</option>
                                            <option value="50">50</option>
                                        </select>&nbsp;</label>
                                </div>
                            </div>
                            <!-- <div class="col-md-6">
                                <div class="text-md-end dataTables_filter" id="dataTable_filter"><label class="form-label"><input type="search" class="form-control form-control-sm" aria-controls="dataTable" placeholder="Search"></label></div>
                            </div> -->
                        </div>
                        {#if state === NetworkStates.loading}
                            <HorizontalLoader />
                        {:else if state === NetworkStates.error}
                            <div class="d-flex w-100 justify-content-center align-items-center py-3" style="color: var(--semi-white); font-style: italic; font-size: larger;">
                                <span>Unable to load data</span>
                            </div>
                        {:else}
                            <div class="table-responsive table mt-2" id="dataTable" role="grid" aria-describedby="dataTable_info">
                                <table class="table my-0" id="dataTable">
                                    <thead>
                                        <tr>
                                            <th class="text-center">S/N</th>
                                            <th class="text-center">Status</th>
                                            <th class="text-center">Amount</th>
                                            <th class="text-center">Timestamp</th>
                                            <th class="text-center">Description</th>
                                        </tr>
                                    </thead>
                                    <tbody>
                                        {#each transactions as transaction, index}
                                            <tr>
                                                <td class="text-center">{((page - 1) * limit) + (index + 1)}</td>
                                                <td class="text-capitalize text-left" class:text-style-info={transaction.status === "pending"} class:text-error={transaction.status === "failed"} class:text-success={transaction.status === "success"}>{transaction.status}</td>
                                                <td class="text-left">{actualCurrency($basicDetails.currency)}{transaction.amount}</td>
                                                <td class="text-left">{formatUTCDate(transaction.date)}</td>
                                                <td class="text-capitalize text-left">{transaction.description}</td>
                                            </tr>
                                        {:else}
                                            <tr>
                                                <td style="font-size: large; font-style: italic;">No transactions yet</td>
                                            </tr>
                                        {/each}
                                    </tbody>
                                </table>
                            </div>
                        {/if}
                        <div class="row">
                            <div class="col-md-6 align-self-center">
                                <p id="dataTable_info" class="dataTables_info" role="status" aria-live="polite">{displayResultsText}</p>
                            </div>
                            <div class="col-md-6">
                                <nav class="d-lg-flex justify-content-lg-end dataTables_paginate paging_simple_numbers">
                                    <ul class="pagination">
                                        <li class="page-item" class:disabled={page === 1}><a class="page-link" aria-label="Previous" href="" on:click={() => {if((page - 1) >= 1) {page--}}}><span aria-hidden="true">«</span></a></li>
                                        {#each pagesRange as pageNumber}
                                            <li class="page-item" class:active={(page === pageNumber)}><a class="page-link" href="" on:click={() => {page = pageNumber}}>{pageNumber}</a></li>
                                        {/each}
                                        <li class="page-item" class:disabled={page === pageCount || pageCount < 2}><a class="page-link" aria-label="Next" href="" on:click={() => {if((page + 1) <= pageCount) {page++}}}><span aria-hidden="true">»</span></a></li>
                                    </ul>
                                </nav>
                            </div>
                        </div>
                    </div>
                </div>
            </div>
        </div>
        <!-- Footer here -->
        <Footer />
    </div>
</div>