<script>
    import Footer from "./../../components/DashboardFooter.svelte";
    import Navigator from "./../../components/DashboardNavigator.svelte";
    import Header from "./../../components/DashboardHeader.svelte";
    import Support from "../../components/settings/support.svelte";
    import General from "../../components/settings/general.svelte";
    import Security from "../../components/settings/security.svelte";
    import LoadingError from "../../components/LoadingError.svelte";
    import Spinkit from "../../components/Spinkit.svelte";
    import { fetchAndSendDataWithJson, NetworkStates, alertUser, basicDetails, updateEmail } from "../../scripts/general_scripts";
    import { onMount } from "svelte";

    let activeTab = 0;
    let submittingData = false;
    let state;
    let errorMessage;

    function setTab(index) {
        if (activeTab !== index) {
            activeTab = index
        }
    }

    let settings = {
        notification: false,
        currency: 'usd',
        language: 'en',
        address: '',
        mobile: '',
        email: '',
    }

    async function updateSettings() {
        submittingData = true;
        let response = await fetchAndSendDataWithJson('/update-settings', settings, 'POST');
        if (response.status) {
            alertUser("", response.message, "success");
            settings = response.data;
            basicDetails.update(d => {
                d.currency = settings.currency
                return {...d};
            })
            if (response.sent_otp) {
                await updateEmail(settings.email);
            }
        } else {
            alertUser("", response.message, "error");
        }
        submittingData = false;
    }

    async function fetchData() {
        state = NetworkStates.loading
        let result = await fetchAndSendDataWithJson("/settings")
        if (result.status) {
            settings = result.data
            state = NetworkStates.loaded
        } else {
            errorMessage = result.message
            state = NetworkStates.error
        }
    }

    onMount(fetchData);
</script>

<div id="wrapper">
    <!-- navigator slot -->
    <Navigator active="settings"/>
    <div class="d-flex flex-column" id="content-wrapper">
        <div id="content" class="background-color">
            <!-- Header slot -->
            <Header />
            <div class="container-fluid">
                <h3 class="mb-1" style="color: var(--white);">Settings</h3>
            </div>
            {#if state == NetworkStates.loading}
                <Spinkit />
            {:else if state == NetworkStates.error}
                <LoadingError message={errorMessage}, on:click={fetchData}/>
            {:else}
                <div class="container">
                    <div class="d-flex flex-wrap mt-3 w-100 justify-content-between">
                        <div class="content-color colu colu-l-2 my-2" style="height: fit-content;">
                            <ul class="settings-navigation">
                                <li><a on:click|preventDefault={() => {setTab(0)}} class:active-link={activeTab === 0} href="">General</a></li>
                                <li><a on:click|preventDefault={() => {setTab(1)}} class:active-link={activeTab === 1} href="">Security</a></li>
                                <li><a on:click|preventDefault={() => {setTab(2)}} class:active-link={activeTab === 2} href="">Support</a></li>
                            </ul>
                        </div>
        
                        <div class="content-color colu colu-l-9 h-75 my-2 p-3">
                            {#if activeTab == 2}
                                <Support bind:notification={settings.notification}/>
                            {:else if activeTab == 0}
                                <General bind:currency={settings.currency} bind:language={settings.language}/>
                            {:else}
                                <Security bind:address={settings.address} bind:mobile={settings.mobile} bind:email={settings.email}/>
                            {/if}

                            <div class="mb-1 mt-3">
                                <button class="btn btn-primary btn-sm" disabled={submittingData} on:click={updateSettings}>
                                    <span class="spinner-grow spinner-grow-sm" class:d-none={!submittingData} role="status" aria-hidden="true"></span> Save Settings
                                </button>
                            </div>
                        </div>
                    </div>
                </div>
            {/if}
        </div>
        <Footer />
    </div>
</div>