<script>
    import Footer from "./../../components/DashboardFooter.svelte";
    import Navigator from "./../../components/DashboardNavigator.svelte";
    import Header from "./../../components/DashboardHeader.svelte";
    import DepositMethod from "../../components/DepositMethod.svelte";
    import DepositConfirmation from "../../components/DepositConfirmation.svelte";

    export let params;

    let amountChosen = 50
    let paymentMethod = "bitcoin"

    $: shouldDisplayPaymentConfirmation = (params !== null && params !== undefined);

    function proceedToConfirmation() {
        location.assign(`/deposit/${paymentMethod}/${amountChosen}`)
    }
</script>

<div id="wrapper">
    <!-- navigator slot -->
    <Navigator active="deposit"/>
    <div class="d-flex flex-column" id="content-wrapper">
        <div id="content" class="background-color">
            <!-- Header slot -->
            <Header />
            <div class="container-fluid">
                <h3 class="mb-1" style="color: var(--white);">Deposit</h3>
            </div>

            <!-- Begining of card to swap out -->
            {#if shouldDisplayPaymentConfirmation}
                <DepositConfirmation method="{params.method}" amount="{params.amount}" />
            {:else}
                <DepositMethod bind:amountChosen bind:paymentMethod on:click={proceedToConfirmation} />
            {/if}
            <!-- End of card to swap out -->
             
        </div>
        <Footer />
    </div>
</div>