<script>
    import { user_verification_status } from "../scripts/general_scripts";
    export let status;

    let objSrc1;
    let objSrc2;

    const idTypes = ["passport", "national", "license"]
    let idType = idTypes[0]

    function transformIDType(value) {
        if (value === "passport") {
            return "International Passport";
        } else if (value === "national") {
            return "National ID";
        } else {
            return "Driver's License"
        }
    }

    function onFileSelected(event) {
        objSrc1 = URL.createObjectURL(event.target.files[0])
    }

    // document.getElementById('idForm').addEventListener('submit', function(event) {
    //     event.preventDefault();
    //     const fileInput = document.getElementById('idUpload');
    //     const fileError = document.getElementById('fileError');
    //     const successMessage = document.getElementById('successMessage');
    //     const maxSizeInMB = 5;
    //     const maxSizeInBytes = maxSizeInMB * 1024 * 1024;

    //     fileError.textContent = '';
    //     successMessage.classList.remove('visible');

    //     if (fileInput.files.length === 0) {
    //         fileError.textContent = 'Please upload a valid ID image or PDF.';
    //     } else if (fileInput.files[0].size > maxSizeInBytes) {
    //         fileError.textContent = `File size exceeds ${maxSizeInMB}MB. Please upload a smaller file.`;
    //     } else {
    //         // Simulate successful upload
    //         successMessage.classList.add('visible');
    //         // You can replace this with actual AJAX file upload
    //     }
    // });
</script>

<div class="body">
    <div class="container">
        <h6 class="text-danger mb-2 text-center" class:d-none={status != user_verification_status.failed}>Verification failed</h6>
        <h2>Verify your identity</h2>
        <p>Please upload a clear front and back image of your ID</p>
        <form id="idForm" method="POST" enctype="multipart/form-data" action="/verify">
            <div class="input-group">
                <p>Select ID Type</p>
                <div class="ms-3">
                    <select class="form-select form-select-sm" aria-label=".form-select-sm example" name="id" id="id" bind:value={idType}>
                        {#each idTypes as type}
                            <option value="{type}">{transformIDType(type)}</option>
                        {/each}
                    </select>
                </div>
            </div>
            <div class="input-group">
                <label for="idUpload">Upload ID</label>
                <input type="file" id="idUpload" name="file" accept="image/* application/pdf" required on:change={onFileSelected}>
                <div class="error" id="fileError"></div>
            </div>
            <div class="preview-container d-none">
                <div>
                    <button type="reset" class="reset">Reset</button>
                </div>
                <div>
                    <embed src={objSrc1} width="100" height="100">
                    <span class="me-3"></span>
                    <embed src={objSrc2} width="100" height="100">
                </div>
            </div>

            <div class="input-group">
                <button type="submit" class="submit-button">
                    <!-- <span class="spinner-grow spinner-grow-sm" role="status" aria-hidden="true"></span> --> Submit for Verification
                </button>
            </div>
        </form>
        <div class="success-message" id="successMessage">
            Your ID has been successfully submitted for verification!
        </div>
    </div>
</div>

<style>
    .body {
        font-family: 'Roboto', sans-serif;
        background-color: inherit;
        margin: 0;
        padding: 0;
        display: flex;
        justify-content: center;
        align-items: center;
        max-height: 100vh;
    }

    .container {
        background: var(--grey);
        padding: 2rem;
        border-radius: 10px;
        box-shadow: 0 5px 15px rgba(0, 0, 0, 0.1);
        max-width: 400px;
        width: 100%;
    }

    .container h2 {
        text-align: center;
        color: var(--white);
        margin-bottom: 1.5rem;
    }

    .container p {
        text-align: center;
        color: var(--semi-white);
        margin-bottom: 2rem;
    }

    .input-group {
        margin-bottom: 1.5rem;
    }

    .input-group label {
        display: block;
        margin-bottom: 0.5rem;
        color: var(--semi-white);
        font-weight: 500;
    }

    .input-group input[type="file"] {
        width: 100%;
        padding: 10px;
        border: 1px solid #ddd;
        border-radius: 5px;
        transition: border-color 0.3s;
    }

    .input-group input[type="file"]:hover {
        border-color: #007bff;
    }

    .input-group .submit-button {
        width: 100%;
        padding: 10px;
        background-color: #007bff;
        border: none;
        border-radius: 5px;
        color: #fff;
        font-weight: 500;
        cursor: pointer;
        transition: background-color 0.3s;
    }

    .input-group .submit-button:hover {
        background-color: #0056b3;
    }

    .input-group .error {
        color: red;
        margin-top: 10px;
    }

    .success-message {
        display: none;
        text-align: center;
        color: #28a745;
        margin-top: 20px;
        font-weight: bold;
    }

    .success-message.visible {
        display: block;
    }

    .reset {
        float: right;
        font-weight: 500;
        color: orangered;
        background-color: inherit;
        height: fit-content;
        width: fit-content;
        padding: 10px;
        border: none;
    }

    .preview-container {
        margin-bottom: 1.5rem;
    }

    .preview {
        max-width: 100px;
        max-height: 100px;
    }
</style>