<script>
    import Footer from "./../../components/DashboardFooter.svelte"
    import Navigator from "./../../components/DashboardNavigator.svelte"
    import Header from "./../../components/DashboardHeader.svelte"
    import Plans from "../../components/modal/plans.svelte"
    import Spinkit from "../../components/Spinkit.svelte"
    import LoadingError from "../../components/LoadingError.svelte"
    import {NetworkStates, alertUser, fetchAndSendDataWithJson, transformPaymentValue, basicDetails, actualCurrency} from "../../scripts/general_scripts"
    import { onMount } from "svelte";

    let state;
    let errorMessage;
    let balance;

    const fetchData = async () => {
        state = NetworkStates.loading
        let result = await fetchAndSendDataWithJson("/purchase-plan")
        if (result.status) {
            balance = result.data.balance
            state = NetworkStates.loaded
        } else {
            errorMessage = result.message
            state = NetworkStates.error
        }
    }
    
    onMount(() => {
        fetchData()
    });

</script>

<div id="wrapper">
    <!-- navigator slot -->
    <Navigator active="purchase"/>
    <div class="d-flex flex-column" id="content-wrapper">
        <div id="content" class="background-color">
            <!-- Header slot -->
            <Header />
            <!-- Content starts here -->
            {#if state === NetworkStates.loaded}
            <div class="container-fluid">
                <div class="justify-content-between align-items-center mb-4">
                    <h3 class="mb-0" style="color: var(--semi-white)">Purchase Plan</h3>
                </div>
                <Plans {balance} />
            </div>
            {:else if state === NetworkStates.error}
                <LoadingError on:click={fetchData} message={errorMessage} />
            {:else}
                <Spinkit />
            {/if}
             <!-- Content ends here -->
        </div>
        <Footer />
    </div>
</div>