<script>
    import Spinkit from "./Spinkit.svelte";
    import LoadingError from "./LoadingError.svelte";
    import { onMount } from "svelte";
    import {transformPaymentValue, alertUser, fileTypes, sendDataAndFile, NetworkStates, fetchAndSendDataWithJson, basicDetails, actualCurrency, referralLink} from "./../scripts/general_scripts";

    export let method;
    export let amount;

    const defaultImageSrc = "/assets/img/untitled.png";

    let fileForm;
    let imgHolder;
    let state;
    let errorMessage;

    let isSubmitable = false;
    let isSubmitting = false;
    let address = '';

    function selectImg() {
        fileForm.file.click();
    }

    function reset() {
        fileForm.file.value = null;
        isSubmitable = false;
        imgHolder.src = defaultImageSrc;
    }

    function onFileSelected(evt) {
        const file = evt.target.files[0]
        if(fileTypes.includes(file.type)){
            imgHolder.src = URL.createObjectURL(file)
            isSubmitable = true;
        }else{
            reset()
            alertUser("File Format", "Invalid Image Format", "error");
        }
    }

    async function submitPayment() {
        isSubmitting = true;
        let result = await sendDataAndFile("/deposit", fileForm, {method, amount})
        isSubmitting = false;
        if (result.status) {
            reset();
            alertUser("", result.message, "success");
        } else {
            alertUser("", result.message, "error");
        }
    }

    const fetchData = async () => {
        // can't include body to a get request
        state = NetworkStates.loading;
        let result = await fetchAndSendDataWithJson(`/deposit/${method}`);
        if (result.status) {
            state = NetworkStates.loaded;
            address = result.data.address;
        } else {
            state = NetworkStates.error;
            errorMessage = result.message;
        }
    }

    onMount(() => {
        fetchData()
    });
</script>

<!--Comment-->
{#if state === NetworkStates.loaded}
    <div class="d-flex justify-content-center align-items-center mb-2">
        <form on:submit|preventDefault={submitPayment} bind:this={fileForm} class="col-12 col-md-4 m-2 px-2 py-3" method="post" style="background: var(--grey);">
            <h4 class="text-center">You are to make a <strong>{actualCurrency($basicDetails.currency)}{amount}</strong> payment using the payment method you selected.</h4>
            <label class="form-label text-end text-capitalize" for="address" style="margin: 0px;font-size: 14px;width: 100%;">{transformPaymentValue(method)} Address</label>
            <div class="form-group mb-3"><input class="form-control" type="text" style="margin: 0px 0px 12px;" name="address" readonly disabled bind:value={address}>
                <input type="file" name="file" id="file" class="d-none" on:change={onFileSelected} accept="image/*" />
                <span class="d-flex justify-content-end w-100 mb-2" style="height: auto;">
                    <a class="me-2" on:click={() => {referralLink(address, false)}} href="">
                        <svg xmlns="http://www.w3.org/2000/svg" width="16" height="16" fill="currentColor" class="bi bi-copy" viewBox="0 0 16 16">
                            <path fill-rule="evenodd" d="M4 2a2 2 0 0 1 2-2h8a2 2 0 0 1 2 2v8a2 2 0 0 1-2 2H6a2 2 0 0 1-2-2zm2-1a1 1 0 0 0-1 1v8a1 1 0 0 0 1 1h8a1 1 0 0 0 1-1V2a1 1 0 0 0-1-1zM2 5a1 1 0 0 0-1 1v8a1 1 0 0 0 1 1h8a1 1 0 0 0 1-1v-1h1v1a2 2 0 0 1-2 2H2a2 2 0 0 1-2-2V6a2 2 0 0 1 2-2h1v1z"/>
                        </svg>
                    </a>
                </span>
                <img src={defaultImageSrc} on:click={selectImg} bind:this={imgHolder} style="display: block;margin-left: auto;margin-right: auto;max-width: 250px;max-height: 250px;" alt=""/>
                <label class="form-label text-center" for="file" style="font-size: 13px;margin: 11px;">Send the exact amount to the wallet supplied above using any platform of your choice. And click the image to upload receipt.</label>
            </div>
            <h6 class="text-center" style="margin: 0px;">You will receive information upon payment confirmation.</h6>
            <div class="text-center">
                <button disabled={!isSubmitable || isSubmitting} class="btn btn-primary" style="width: 50%;margin: 30px 0px 0px;padding: 16px;" type="submit">
                    <span class="spinner-grow spinner-grow-sm" class:d-none={!isSubmitting} role="status" aria-hidden="true"></span> submit payment
                </button>
            </div>
        </form>
    </div>
{:else if state === NetworkStates.loading}
    <Spinkit />
{:else}
    <LoadingError  message={errorMessage} on:click={fetchData} />
{/if}