<script>
    import { fetchAndSendDataWithJson, alertUser } from "../../scripts/general_scripts";

    export let notification;

    let isSubmittingMessageToSupport = false;

    let contactSupportData = {
        message: "",
        notifyMe: notification
    };

    const submitMessageToSupport = async (evt) => {
        isSubmittingMessageToSupport = true;
        let result = await fetchAndSendDataWithJson("/user-contact", contactSupportData, 'POST')
        isSubmittingMessageToSupport = false
        if (result.status) {
            evt.target.reset();
            alertUser("", result.message, "success");
        } else {
            alertUser("Message sending failed", result.message, "error");
        }
    }
</script>

<div>
    <div class="mb-3 mt-1 d-flex justify-content-between">
        <p class="fw-bolder">Notifications</p>

        <div class="form-check form-switch">
            <input class="form-check-input" type="checkbox" id="formCheck-1" name="notification" bind:checked={notification}>
        </div>
    </div>

    <div class="card shadow mb-5 content-color">
        <div class="card-header py-3 content-color">
            <p class="m-0 fw-bold">Contact Support</p>
        </div>
        <div class="card-body">
            <div class="row">
                <div class="col-md-6 content-color">
                    <form on:submit|preventDefault={submitMessageToSupport}>
                        <div class="mb-3"><label class="form-label" for="signature"><strong>Message</strong></label><textarea required class="form-control" id="signature" rows="4" name="message" style="background: inherit; color: var(--semi-white)" bind:value={contactSupportData.message}></textarea></div>
                        <div class="mb-3">
                            <button class="btn btn-primary btn-sm" type="submit" disabled={isSubmittingMessageToSupport}>
                                <span class="spinner-grow spinner-grow-sm" class:d-none={!isSubmittingMessageToSupport} role="status" aria-hidden="true"></span> Send
                            </button>
                        </div>
                    </form>
                </div>
            </div>
        </div>
    </div>
</div>