<script>
    import Footer from "./../../components/DashboardFooter.svelte"
    import Navigator from "./../../components/DashboardNavigator.svelte"
    import Header from "./../../components/DashboardHeader.svelte"
    import Spinkit from "../../components/Spinkit.svelte"
    import LoadingError from "../../components/LoadingError.svelte"
    import {NetworkStates, alertUser, fetchAndSendDataWithJson, transformPaymentValue, basicDetails, actualCurrency} from "../../scripts/general_scripts"
    import { onMount } from "svelte";

    let paymentMethods = []
    let values = {
        address: "",
        amount: 0,
        method: ""
    }

    let state;
    let errorMessage;
    let balance;
    let isSubmitting = false

    const fetchData = async () => {
        state = NetworkStates.loading
        let result = await fetchAndSendDataWithJson("/withdraw")
        if (result.status) {
            balance = result.data.balance
            paymentMethods = result.data.methods
            state = NetworkStates.loaded
        } else {
            errorMessage = result.message
            state = NetworkStates.error
        }
    }

    const submitRequest = async(evt) => {
        isSubmitting = true
        let result = await fetchAndSendDataWithJson("/withdraw", values, 'POST')
        isSubmitting = false
        if(result.status) {
            values.address = ""
            values.amount = 0
            values.method = ""
            evt.target.reset()
            alertUser("", result.message, "success")
        } else {
            alertUser("", result.message, "error")
        }
    }
    
    onMount(() => {
        fetchData()
    });

</script>

<div id="wrapper">
    <!-- navigator slot -->
    <Navigator active="withdraw"/>
    <div class="d-flex flex-column" id="content-wrapper">
        <div id="content" class="background-color">
            <!-- Header slot -->
            <Header />
            <!-- Content starts here -->
            {#if state === NetworkStates.loaded}
                <div class="body">
                    <div class="container">
                    <h1 class="center-text text-uppercase">Withdraw to personal wallet</h1>
                    <div class="balance center-text">
                        Balance: <span id="balance">{actualCurrency($basicDetails.currency)}{balance}</span>
                    </div>
                    <form on:submit|preventDefault={submitRequest}>
                        <input bind:value={values.address} class="get-started-input-fields" type="text" id="wallet-address" name="wallet-address" required placeholder="Wallet Address">
                
                        <input bind:value={values.amount} class="get-started-input-fields mt-2" type="number" id="amount" name="amount" required placeholder="Amount">
                
                        <p class="label text-capitalize text-align-start" for="crypto-type">preferred payment method</p>
                        <div class="selectgroup selectgroup-pills mt-1 mb-3">
                            {#each paymentMethods as payment}
                                <label class="selectgroup-item my-2 align-self-start">
                                    <input type="radio" name="payment-method" value={payment} bind:group={values.method} class="selectgroup-input" />
                                    <span class="selectgroup-button text-uppercase">{transformPaymentValue(payment)}</span>
                                </label>
                            {/each}
                        </div>

                        <!-- <select class="get-started-input-fields text-capitalize" name="method" bind:value={values.method}>
                            {#each paymentMethods as method}
                                <option value={method}>{transformPaymentValue(method)}</option>
                            {/each}
                        </select> -->
                
                        <div class="center-text mt-3">
                            <button type="submit" class="button" disabled={isSubmitting}>
                                <span class="spinner-grow spinner-grow-sm" class:d-none={!isSubmitting} role="status" aria-hidden="true"></span> Withdraw
                            </button>
                        </div>
                    </form>
                    <div class="footer-t center-text">
                        Please ensure the wallet address is correct to avoid losing funds.
                    </div>
                    </div>
                </div>
            {:else if state === NetworkStates.error}
                <LoadingError on:click={fetchData} message={errorMessage} />
            {:else}
                <Spinkit />
            {/if}
             <!-- Content ends here -->
        </div>
        <Footer />
    </div>
</div>

<style>
    .get-started-input-fields option {
        background: var(--grey);
        color: var(--ghost);
    }

    .body {
      display: flex;
      justify-content: start;
      align-items: center;
      height: 100vh;
      margin: 0;
      background: inherit;
      font-family: Arial, sans-serif;
      color: #ffffff;
    }
    .container {
      background-color: var(--grey);
      padding: 20px;
      border-radius: 10px;
      box-shadow: 0 8px 16px rgba(0, 0, 0, 0.3);
      width: 400px;
      text-align: start;
    }
    h1 {
      margin-bottom: 20px;
      font-size: 24px;
    }
    .balance {
      font-size: 18px;
      margin-bottom: 20px;
    }
    .label {
      display: block;
      margin: 10px 0 5px;
    }
    input[type="text"], input[type="number"] {
      width: calc(100% - 20px);
      margin-bottom: 10px;
    }

    .button {
      background-color: #4caf50;
      color: white;
      padding: 10px 20px;
      border: none;
      border-radius: 5px;
      cursor: pointer;
      font-size: 16px;
      margin-top: 10px;
    }
    .button:hover {
      background-color: #45a049;
    }
    .footer-t {
      margin-top: 20px;
      font-size: 14px;
      color: #cccccc;
    }
    .center-text {
        text-align: center;
    }
</style>