<script>
    import {transformPaymentValue} from "./../scripts/general_scripts"
    import { basicDetails, actualCurrency } from "./../scripts/general_scripts";

    let amountList = [50, 100, 200, 500, 1000, 2000, 5000]

    export let amountChosen
    export let paymentMethod

    $: disableProceedButton = (amountChosen == null || amountChosen < 50 )
</script>

<div class="d-flex justify-content-center align-items-center flex-wrap px-3 py-2 mb-3">
    <div class="card" style="background: var(--grey);">
        <div class="card-body">
            <div class="container">
                <div class="row">
                    <div class="col-md-6">
                        <div class="selectgroup selectgroup-pills">
                            {#each amountList as amount}
                                <label class="selectgroup-item my-2">
                                    <input type="radio" name="amount" value={amount} class="selectgroup-input" bind:group={amountChosen}/>
                                    <span class="selectgroup-button">{actualCurrency($basicDetails.currency)}{amount}</span>
                                </label>
                            {/each}
                        </div>
                        <div class="py-3">
                            <p style="color: var(--ghost);">Enter Amount</p>
                            <input class="get-started-input-fields" type="number" style="width:100%; min-height: 40px;" bind:value={amountChosen} />
                        </div>
                        <div class="my-3">
                            <p style="color: var(--ghost);">Select preferred payment method</p>
                            <div class="selectgroup selectgroup-pills">
                                {#each $basicDetails.paymentMethods as payment}
                                    <label class="selectgroup-item my-2">
                                        <input type="radio" name="payment-method" value={payment} bind:group={paymentMethod} class="selectgroup-input" />
                                        <span class="selectgroup-button text-uppercase">{transformPaymentValue(payment)}</span>
                                    </label>
                                {/each}
                            </div>
                        </div>
                    </div>
                    <div class="col-md-6">
                        <h3 class="text-uppercase" style="color: var(--ghost);">Deposit Summary</h3>
                        <h5 style="color: var(--semi-white);"><strong>Amount:</strong> <em>{actualCurrency($basicDetails.currency)}{amountChosen == null ? 0 : amountChosen}</em></h5>
                        <h5 style="color: var(--semi-white);"><strong>Payment method: </strong><em class="text-uppercase" style="font-size: 16px;">{transformPaymentValue(paymentMethod)}</em></h5>
                    </div>
                </div>
            </div>
            <div class="container">
                <div class="row">
                    <div class="col-md-12 text-center"><button disabled={disableProceedButton} class="btn btn-primary" type="button" style="min-width: 250px;" on:click>Proceed with payment</button></div>
                </div>
            </div>
        </div>
    </div>
</div>