export function showSnackbar(type, message) {
    const snackbar = document.getElementById('snackbar');
    const snackbarBody = document.getElementById("snackbar-body");
    const snackbarMessage = document.getElementById('snackbar-message');
    const successIcon = document.querySelector('.icon.success');
    const errorIcon = document.querySelector('.icon.error');

    snackbar.className = 'snackbar show ' + type;
    snackbar.classList.toggle("d-none");
    snackbarMessage.textContent = message;

    if (type === 'success') {
      successIcon.style.display = 'block';
      errorIcon.style.display = 'none';
    } else if (type === 'error') {
      successIcon.style.display = 'none';
      errorIcon.style.display = 'block';
    }

    setTimeout(() => {
      snackbar.className = snackbar.className.replace('show', '');
      snackbar.classList.toggle("d-none");
    }, 3000);
}

export function transformPaymentValue(value) {
    if (value === "usdt_trx") {
        return "usdt trc"
    } else if (value === "usdt_eth") {
        return "usdt eth"
    } else {
        return value
    }
}

