export const fetchAndSendDataWithJson = async (path, data = null, requestMethod = 'GET') => {
    let json = (data === null ? data : JSON.stringify(data));

    try {
        let response = await fetch(/*`http://localhost:3010${path}`*/path, {
            method: requestMethod,
            credentials: 'include',
            body: json,
            headers: {
                'Content-Type': 'application/json',
                "fetch-data": "true"
            }
        });
        let result = await response.json();
        return result;
    } catch (error) {
        console.log(error);
        return {status: false, message: "Unable to reach server", data: {}}
    }
}

export const fetchAndSendDataWithJsonAndQuery = async (path, query, data = null, requestMethod = 'GET') => {
    const sortedCheck = (query || {})
    let queryString = `${path}?`;
    Object.entries(sortedCheck).forEach(([key, value], index) => {
        if (index > 0) {
            queryString = `${queryString}&${key}=${value}`
        } else {
            queryString = `${queryString}${key}=${value}`
        }
    });
    let result = await fetchAndSendDataWithJson(queryString, data, requestMethod);
    return result;
}

export const sendDataAndFile = async (path, data, additionalData = null) => {
    let formData = new FormData(data);
    Object.entries((additionalData || {})).forEach(([key, value]) => {
        formData.append(key, value);
    });

    try{
        let response = await fetch(/*`http://localhost:3010${path}`*/path, {
            method: 'POST',
            credentials: 'include',
            body: formData
        });
        let result = await response.json();
        return result;
    }catch (error){
        console.log(error);
        return {status: false, message: "Unable to reach server", data: {}}
    }
}

