
<div class="body background-color">
    <div class="container">
        <div class="spinner">
            <svg xmlns="http://www.w3.org/2000/svg" xmlns:xlink="http://www.w3.org/1999/xlink" style="margin:auto;background:none;display:block;" width="100px" height="100px" viewBox="0 0 100 100" preserveAspectRatio="xMidYMid">
                <circle cx="50" cy="50" fill="none" stroke="#1cc88a" stroke-width="10" r="35" stroke-dasharray="164.93361431346415 56.97787143782138">
                  <animateTransform attributeName="transform" type="rotate" repeatCount="indefinite" dur="1s" keyTimes="0;1" values="0 50 50;360 50 50"></animateTransform>
                </circle>
              </svg>
        </div>
        <h1>Verification Ongoing</h1>
        <p>We will notify soon when a decision is made</p>
    </div>
</div>

<style>
    .body {
        display: flex;
        justify-content: center;
        align-items: center;
        height: 90vh;
        margin: 0;
        font-family: Arial, sans-serif;
    }

    .container {
        text-align: center;
    }

    h1 {
        color: var(--white);
        font-size: 24px;
        margin-top: 20px;
    }

    p {
        color: var(--semi-white);
        font-size: 18px;
        margin-top: 10px;
    }

    .spinner {
        width: 100px;
        height: 100px;
        margin: 0 auto;
    }

    .spinner svg {
        animation: rotate 2s linear infinite;
    }

    @keyframes rotate {
        100% {
            transform: rotate(360deg);
        }
    }
</style>
