<script>
    import { changeLanguage } from "../../scripts/general_scripts";

    export let currency;
    export let language;

    $: changeLanguage(language)
</script>

<div>
    <div class="d-flex justify-content-between align-items-center my-2 align-middle">
        <p class="fw-bolder">Default Currency</p>
        <select name="currency" id="currency" bind:value={currency}>
            <option value="eur">Euro €</option>
            <option value="gbp">GBP £</option>
            <option value="usd">USD $</option>
        </select>
    </div>

    <div class="d-flex justify-content-between my-2 align-middle d-none">
        <p class="fw-bolder">Default Language</p>
        <div class="d-none" id="google_translate_element"></div>
        <select name="language" id="language" class="notranslate" translate="no" bind:value={language}>
            <option value="en">English</option>
            <option value="es">Spanish</option>
            <option value="fr">French</option>
            <option value="de">German</option>
            <option value="zh-CN">Chinese</option>
            <option value="ru">Russian</option>
            <option value="ja">Japanese</option>
            <option value="ar">Arabic</option>
            <option value="pt">Portuguese</option>
            <option value="hi">Hindi</option>
        </select>
    </div>
</div>

<style>
    select {
        border: none !important;
        background: inherit !important;
        font-weight: 800;
        font-size: 18px;
        color: var(--semi-white);
    }

    select option {
        border: none !important;
        background: var(--grey)
    }
</style>