<script>
    import { onMount } from "svelte";
    import { fetchAndSendDataWithJson, alertUser, basicDetails } from "../../scripts/general_scripts";

    export let address;
    export let mobile;
    export let email;

    let editAddress = false;
    let editMobile = false;
    let editEmail = false;

    $: editAddressTitle = editAddress ? "Cancel" : "Change";
    $: editMobileTitle = editMobile ? "Cancel" : "Reset";

    async function resetPassword() {
        let result = await fetchAndSendDataWithJson("/reset-password-mail", {email: $basicDetails.email}, 'POST')
        alertUser("", result.message);
    }

    onMount(() => {
        const input = document.getElementById("phone")
        window.intlTelInput(input, {
            utilsScript: "https://cdn.jsdelivr.net/npm/intl-tel-input@24.0.1/build/js/utils.js",
        });
    });
</script>

<div>
    <div class="d-flex justify-content-between align-items-center my-2 align-middle">
        <input class="get-started-input-fields text-capitalize me-4" class:borderless-input={!editAddress} disabled={!editAddress} type="text" placeholder="address" name="address" bind:value={address}>
        <button class="text-button" on:click={() => {editAddress = !editAddress}}>
            {editAddressTitle}
        </button>
    </div>

    <div class="d-flex justify-content-between align-items-center my-2 align-middle">
        <input id="phone" class="get-started-input-fields me-4" class:borderless-input={!editMobile} disabled={!editMobile} type="text" placeholder="phone" name="mobile" bind:value={mobile} pattern="^\+?\d{1,4}?[-.\s]?\(?\d{1,5}?\)?[-.\s]?\d{1,4}[-.\s]?\d{1,4}[-.\s]?\d{1,9}$">
        <button class="text-button" on:click={() => {editMobile = !editMobile}}>
            {editMobileTitle}
        </button>
    </div>

    <div class="d-flex justify-content-between align-items-center my-2 align-middle">
        <input class="get-started-input-fields me-4" class:borderless-input={!editEmail} disabled={!editEmail} type="email" placeholder="email" name="email" bind:value={email}>
        <button class="text-button" on:click={() => {editEmail = !editEmail}}>
            Reset
        </button>
    </div>

    <div class="d-flex justify-content-between align-items-center my-2 align-middle">
        <input class="get-started-input-fields me-4 colu-5 borderless-input" disabled type="text" placeholder="**********" name="password">
        <button class="text-button" on:click={() => {resetPassword()}}>
            Reset Password
        </button>
    </div>
</div>